@import url(https://fonts.googleapis.com/css2?family=Play:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);

body,html{margin:0;padding:0}body{color:#222831;display:flex;justify-content:center;align-items:center;font-family:"Play", sans-serif;text-align:center;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content;font-family:"Press Start 2P", cursive}

#editor{border:1px solid #222831;border-radius:0.25rem;padding:1rem 4rem 2rem 4rem;margin-top:2rem;display:flex;flex-direction:column;align-items:center}#editor h1{margin-bottom:1.5rem}#editor h2{margin-bottom:1rem}#editor #options{display:flex;margin-bottom:3rem;justify-content:center}#editor #options .option{display:flex;flex-direction:column}#editor #options .panelInput{height:5rem;width:5rem;font-size:1.3rem;text-align:center;background-color:#222831;color:#e8e8e8;border:1px solid #30475e;border-radius:0.25rem;margin:0 1rem 0.5rem 1rem;padding-left:1rem}#editor #options .panelInput:focus{outline:none !important;border-color:#f05454;box-shadow:0 0 0.5rem #30475e}#editor .button{font-size:1.25rem;background-color:#222831;color:#e8e8e8;padding:1rem 3rem;border-radius:0.25rem;text-transform:uppercase;border:1px solid transparent;transition:background-color 0.2s ease-in-out;margin-bottom:2rem}#editor .button:hover{cursor:pointer;background-color:transparent;border-color:#222831}#editor .circle-picker{margin-bottom:1.5rem !important}@media only screen and (min-device-width: 320px) and (max-device-width: 667px){#editor{padding:0;margin-top:0}#options .button{width:50%}h1{text-align:center}h2{text-align:center}}

#drawingPanel{display:flex;flex-direction:column;align-items:center;margin-top:10px}#pixels{margin-bottom:2rem}

.row{display:flex;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}

.pixel{width:1.5rem;height:1.5rem;background:#8cc6db}@media only screen and (min-device-width: 300px) and (max-device-width: 667px){#editor .pixel{width:0.5rem;height:0.5rem}}

